"use client"

import * as React from "react"
import { cn } from "../lib/utils"

export interface PaginationProps extends React.HTMLAttributes<HTMLDivElement> {
  currentPage: number
  totalPages: number
  onPageChange: (page: number) => void
  showFirstLast?: boolean
  showPrevNext?: boolean
  maxVisiblePages?: number
  size?: "sm" | "md" | "lg"
  variant?: "default" | "outlined" | "minimal"
  shape?: "square" | "circle"
}

const Pagination = React.forwardRef<HTMLDivElement, PaginationProps>(
  ({ 
    className, 
    currentPage,
    totalPages,
    onPageChange,
    showFirstLast = true,
    showPrevNext = true,
    maxVisiblePages = 5,
    size = "md",
    variant = "default",
    shape = "square",
    ...props 
  }, ref) => {
    const getVisiblePages = () => {
      const pages: (number | string)[] = []
      const halfVisible = Math.floor(maxVisiblePages / 2)
      
      let start = Math.max(1, currentPage - halfVisible)
      let end = Math.min(totalPages, currentPage + halfVisible)
      
      // 
      if (end - start + 1 < maxVisiblePages) {
        if (start === 1) {
          end = Math.min(totalPages, start + maxVisiblePages - 1)
        } else {
          start = Math.max(1, end - maxVisiblePages + 1)
        }
      }
      
      //  
      if (start > 1) {
        pages.push(1)
        if (start > 2) {
          pages.push("...")
        }
      }
      
      //  
      for (let i = start; i <= end; i++) {
        pages.push(i)
      }
      
      //  
      if (end < totalPages) {
        if (end < totalPages - 1) {
          pages.push("...")
        }
        pages.push(totalPages)
      }
      
      return pages
    }

    const getSizeClasses = () => {
      switch (size) {
        case "sm":
          return "h-8 px-2 text-sm" // 32px , 8px 
        case "lg":
          return "h-12 px-4 text-base" // 48px , 16px 
        default:
          return "h-10 px-3 text-sm" // 40px , 12px 
      }
    }

    const getShapeClasses = () => {
      switch (shape) {
        case "circle":
          return "rounded-full aspect-square flex items-center justify-center"
        default:
          return "rounded-md"
      }
    }

    const getVariantClasses = (isActive: boolean = false) => {
      const activeClasses =
        "bg-brand-primary text-white border-brand-primary shadow-sm";
      const hoverAccent =
        "hover:border-brand-primary hover:text-brand-primary focus-visible:ring-brand-primary/40";

      switch (variant) {
        case "outlined":
          return cn(
            "border bg-white text-gray-700 transition-colors dark:border-gray-600 dark:bg-gray-800 dark:text-gray-300",
            hoverAccent,
            isActive
              ? activeClasses
              : "border-gray-200 dark:border-gray-600 hover:bg-white dark:hover:bg-gray-700"
          );
        case "minimal":
          return cn(
            "border-0 bg-transparent text-gray-600 transition-colors dark:text-gray-300",
            "hover:bg-gray-100 dark:hover:bg-gray-700",
            hoverAccent,
            isActive
              ? "bg-brand-primary/10 text-brand-primary"
              : undefined
          );
        default:
          return cn(
            "border border-transparent bg-gray-50 text-gray-700 transition-colors dark:bg-gray-800 dark:text-gray-300",
            hoverAccent,
            isActive ? activeClasses : "hover:bg-white dark:hover:bg-gray-700"
          );
      }
    };

    const handlePageClick = (page: number) => {
      if (page >= 1 && page <= totalPages && page !== currentPage) {
        onPageChange(page)
      }
    }

    const visiblePages = getVisiblePages()

    return (
      <div
        ref={ref}
        className={cn(
          "flex items-center justify-center gap-1",
          className
        )}
        {...props}
      >
        {/*    */}
        {showFirstLast && currentPage > 1 && (
          <button
            onClick={() => handlePageClick(1)}
            className={cn(
              "inline-flex items-center justify-center font-medium transition-colors focus-visible:outline-none focus-visible:ring-2 focus-visible:ring-offset-2 disabled:pointer-events-none disabled:opacity-50",
              getSizeClasses(),
              getShapeClasses(),
              getVariantClasses()
            )}
            aria-label="  "
          >
            <svg className="w-4 h-4" fill="none" stroke="currentColor" viewBox="0 0 24 24">
              <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M11 19l-7-7 7-7M19 19l-7-7 7-7" />
            </svg>
          </button>
        )}

        {/*    */}
        {showPrevNext && currentPage > 1 && (
          <button
            onClick={() => handlePageClick(currentPage - 1)}
            className={cn(
              "inline-flex items-center justify-center font-medium transition-colors focus-visible:outline-none focus-visible:ring-2 focus-visible:ring-offset-2 disabled:pointer-events-none disabled:opacity-50",
              getSizeClasses(),
              getShapeClasses(),
              getVariantClasses()
            )}
            aria-label="  "
          >
            <svg className="w-4 h-4" fill="none" stroke="currentColor" viewBox="0 0 24 24">
              <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M15 19l-7-7 7-7" />
            </svg>
          </button>
        )}

        {/*   */}
        {visiblePages.map((page, index) => (
          <React.Fragment key={index}>
            {page === "..." ? (
              <span className={cn(
                "inline-flex items-center justify-center px-3 py-2 text-sm font-medium text-gray-500 dark:text-gray-400",
                getSizeClasses()
              )}>
                ...
              </span>
            ) : (
              <button
                onClick={() => handlePageClick(page as number)}
                className={cn(
                  "inline-flex items-center justify-center font-medium transition-colors focus-visible:outline-none focus-visible:ring-2 focus-visible:ring-offset-2 disabled:pointer-events-none disabled:opacity-50",
                  getSizeClasses(),
                  getShapeClasses(),
                  getVariantClasses(page === currentPage)
                )}
                aria-label={`${page} `}
                aria-current={page === currentPage ? "page" : undefined}
              >
                {page}
              </button>
            )}
          </React.Fragment>
        ))}

        {/*    */}
        {showPrevNext && currentPage < totalPages && (
          <button
            onClick={() => handlePageClick(currentPage + 1)}
            className={cn(
              "inline-flex items-center justify-center font-medium transition-colors focus-visible:outline-none focus-visible:ring-2 focus-visible:ring-offset-2 disabled:pointer-events-none disabled:opacity-50",
              getSizeClasses(),
              getShapeClasses(),
              getVariantClasses()
            )}
            aria-label="  "
          >
            <svg className="w-4 h-4" fill="none" stroke="currentColor" viewBox="0 0 24 24">
              <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M9 5l7 7-7 7" />
            </svg>
          </button>
        )}

        {/*    */}
        {showFirstLast && currentPage < totalPages && (
          <button
            onClick={() => handlePageClick(totalPages)}
            className={cn(
              "inline-flex items-center justify-center font-medium transition-colors focus-visible:outline-none focus-visible:ring-2 focus-visible:ring-offset-2 disabled:pointer-events-none disabled:opacity-50",
              getSizeClasses(),
              getShapeClasses(),
              getVariantClasses()
            )}
            aria-label="  "
          >
            <svg className="w-4 h-4" fill="none" stroke="currentColor" viewBox="0 0 24 24">
              <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M13 5l7 7-7 7M5 5l7 7-7 7" />
            </svg>
          </button>
        )}
      </div>
    )
  }
)
Pagination.displayName = "Pagination"

//  
export const PaginationOutlined = React.forwardRef<HTMLDivElement, Omit<PaginationProps, "variant">>(
  ({ className, ...props }, ref) => (
    <Pagination ref={ref} variant="outlined" className={className} {...props} />
  )
)
PaginationOutlined.displayName = "PaginationOutlined"

export const PaginationMinimal = React.forwardRef<HTMLDivElement, Omit<PaginationProps, "variant">>(
  ({ className, ...props }, ref) => (
    <Pagination ref={ref} variant="minimal" className={className} {...props} />
  )
)
PaginationMinimal.displayName = "PaginationMinimal"

//  
export const PaginationWithInfo = React.forwardRef<HTMLDivElement, PaginationProps & { 
  totalItems?: number
  itemsPerPage?: number
  showInfo?: boolean
}>(
  ({ 
    totalItems = 0, 
    itemsPerPage = 10, 
    showInfo = true, 
    className, 
    ...props 
  }, ref) => {
    const startItem = (props.currentPage - 1) * itemsPerPage + 1
    const endItem = Math.min(props.currentPage * itemsPerPage, totalItems)
    
    return (
      <div className={cn("flex flex-col sm:flex-row items-center justify-between gap-4", className)}>
        {showInfo && (
          <div className="text-sm text-gray-700 dark:text-gray-300">
            {totalItems > 0 ? (
              <>
                <span className="font-medium">{startItem}</span>
                {" - "}
                <span className="font-medium">{endItem}</span>
                {" of "}
                <span className="font-medium">{totalItems}</span>
                {" results"}
              </>
            ) : (
              "No results"
            )}
          </div>
        )}
        <Pagination ref={ref} {...props} />
      </div>
    )
  }
)
PaginationWithInfo.displayName = "PaginationWithInfo"

export { Pagination } 