"use client"

import * as React from "react"
import { cn } from "../lib/utils"
import type { DivProps } from "../types/props"

export type DropdownProps = DivProps & {
  trigger: React.ReactNode
  children: React.ReactNode
  open?: boolean
  onOpenChange?: (open: boolean) => void
  placement?: "top" | "bottom" | "left" | "right"
  align?: "start" | "center" | "end"
  offset?: number
  disabled?: boolean
  showArrow?: boolean
}

const Dropdown = React.forwardRef<HTMLDivElement, DropdownProps>(
  ({ 
    className, 
    trigger,
    children,
    open: controlledOpen,
    onOpenChange,
    placement = "bottom",
    align = "start",
    offset = 8,
    disabled = false,
    showArrow = true,
    ...props 
  }, ref) => {
    const [internalOpen, setInternalOpen] = React.useState(false)
    const [coords, setCoords] = React.useState({ x: 0, y: 0 })
    const triggerRef = React.useRef<HTMLDivElement>(null)
    const dropdownRef = React.useRef<HTMLDivElement>(null)
    const isControlled = controlledOpen !== undefined
    const isOpen = isControlled ? controlledOpen : internalOpen

    const handleOpenChange = (newOpen: boolean) => {
      if (disabled) return
      
      if (!isControlled) {
        setInternalOpen(newOpen)
      }
      onOpenChange?.(newOpen)
    }

    const handleTriggerClick = () => {
      handleOpenChange(!isOpen)
    }

    const updatePosition = React.useCallback(() => {
      if (!triggerRef.current || !dropdownRef.current) return

      const triggerRect = triggerRef.current.getBoundingClientRect()
      const dropdownRect = dropdownRef.current.getBoundingClientRect()
      const viewportWidth = window.innerWidth
      const viewportHeight = window.innerHeight

      let x = 0
      let y = 0

      //   
      switch (placement) {
        case "top":
          x = triggerRect.left
          y = triggerRect.top - offset
          break
        case "bottom":
          x = triggerRect.left
          y = triggerRect.bottom + offset
          break
        case "left":
          x = triggerRect.left - offset
          y = triggerRect.top
          break
        case "right":
          x = triggerRect.right + offset
          y = triggerRect.top
          break
      }

      //  
      switch (align) {
        case "center":
          if (placement === "top" || placement === "bottom") {
            x = triggerRect.left + triggerRect.width / 2 - dropdownRect.width / 2
          } else {
            y = triggerRect.top + triggerRect.height / 2 - dropdownRect.height / 2
          }
          break
        case "end":
          if (placement === "top" || placement === "bottom") {
            x = triggerRect.right - dropdownRect.width
          } else {
            y = triggerRect.bottom - dropdownRect.height
          }
          break
        case "start":
        default:
          //   start 
          break
      }

      //     
      if (x < 8) x = 8 // 8px 
      if (x + dropdownRect.width > viewportWidth - 8) {
        x = viewportWidth - dropdownRect.width - 8 // 8px 
      }
      if (y < 8) y = 8 // 8px 
      if (y + dropdownRect.height > viewportHeight - 8) {
        y = viewportHeight - dropdownRect.height - 8 // 8px 
      }

      setCoords({ x, y })
    }, [placement, align, offset])

    React.useEffect(() => {
      if (isOpen) {
        updatePosition()
        window.addEventListener('resize', updatePosition)
        window.addEventListener('scroll', updatePosition)
        
        return () => {
          window.removeEventListener('resize', updatePosition)
          window.removeEventListener('scroll', updatePosition)
        }
      }
    }, [isOpen, updatePosition])

    React.useEffect(() => {
      const handleClickOutside = (event: MouseEvent) => {
        if (
          triggerRef.current && 
          dropdownRef.current && 
          !triggerRef.current.contains(event.target as Node) &&
          !dropdownRef.current.contains(event.target as Node)
        ) {
          handleOpenChange(false)
        }
      }

      if (isOpen) {
        document.addEventListener('mousedown', handleClickOutside)
        return () => {
          document.removeEventListener('mousedown', handleClickOutside)
        }
      }
    }, [isOpen])

    const getPlacementClasses = () => {
      switch (placement) {
        case "top":
          return "bottom-full left-0 mb-2" // 8px 
        case "bottom":
          return "top-full left-0 mt-2" // 8px 
        case "left":
          return "right-full top-0 mr-2" // 8px 
        case "right":
          return "left-full top-0 ml-2" // 8px 
        default:
          return "top-full left-0 mt-2"
      }
    }

    const getArrowClasses = () => {
      switch (placement) {
        case "top":
          return "top-full left-4 -translate-x-1/2 border-t-gray-100 dark:border-t-gray-800"
        case "bottom":
          return "bottom-full left-4 -translate-x-1/2 border-b-gray-100 dark:border-b-gray-800"
        case "left":
          return "left-full top-4 -translate-y-1/2 border-l-gray-100 dark:border-l-gray-800"
        case "right":
          return "right-full top-4 -translate-y-1/2 border-r-gray-100 dark:border-r-gray-800"
        default:
          return "bottom-full left-4 -translate-x-1/2 border-b-gray-100 dark:border-b-gray-800"
      }
    }

    return (
      <div ref={ref} className={cn("relative", className)} {...props}>
        {/*  */}
        <div
          ref={triggerRef}
          onClick={handleTriggerClick}
          className="inline-block cursor-pointer"
        >
          {trigger}
        </div>

        {/*  */}
        {isOpen && (
          <>
            {/*  (  ) */}
            <div
              className="fixed inset-0 z-40"
              onClick={() => handleOpenChange(false)}
            />
            
            <div
              ref={dropdownRef}
              className={cn(
                "absolute z-50 bg-white dark:bg-gray-800 rounded-lg shadow-xl border border-gray-200 dark:border-gray-700",
                "min-w-[200px] max-w-[320px] py-1.5",
                "overflow-hidden",
                getPlacementClasses()
              )}
              style={{
                transform: `translate(${coords.x}px, ${coords.y}px)`,
                boxShadow: "0 10px 15px -3px rgba(0, 0, 0, 0.1), 0 4px 6px -2px rgba(0, 0, 0, 0.05)"
              }}
            >
              {/*  */}
              {showArrow && (
                <div
                  className={cn(
                    "absolute w-0 h-0 border-4 border-transparent",
                    getArrowClasses()
                  )}
                />
              )}
              
              {/*  */}
              <div className="relative z-10">
                {children}
              </div>
            </div>
          </>
        )}
      </div>
    )
  }
)
Dropdown.displayName = "Dropdown"

//   
export interface DropdownItemProps extends React.ButtonHTMLAttributes<HTMLButtonElement> {
  icon?: React.ReactNode
  rightIcon?: React.ReactNode
  variant?: "default" | "destructive" | "disabled"
}

const DropdownItem = React.forwardRef<HTMLButtonElement, DropdownItemProps>(
  ({ 
    className, 
    icon,
    rightIcon,
    variant = "default",
    children,
    disabled,
    ...props 
  }, ref) => {
    const getVariantClasses = () => {
      switch (variant) {
        case "destructive":
          return "text-red-600 dark:text-red-400 hover:bg-red-50 dark:hover:bg-red-900/20"
        case "disabled":
          return "text-gray-400 dark:text-gray-500 cursor-not-allowed"
        default:
          return "text-gray-700 dark:text-gray-300 hover:bg-gray-100 dark:hover:bg-gray-700"
      }
    }

    return (
      <button
        ref={ref}
        className={cn(
          "w-full flex items-center gap-3 px-4 py-2.5 text-sm font-medium transition-colors",
          "focus:outline-none focus:bg-gray-100 dark:focus:bg-gray-700",
          "disabled:opacity-50 disabled:cursor-not-allowed",
          getVariantClasses(),
          className
        )}
        disabled={disabled || variant === "disabled"}
        {...props}
      >
        {/*   */}
        {icon && (
          <span className="flex-shrink-0 flex items-center justify-center w-4 h-4 text-current">
            {icon}
          </span>
        )}
        
        {/*   */}
        <span className={cn(
          "flex-1 text-left min-w-0",
          icon ? "" : rightIcon ? "" : ""
        )}>
          {children}
        </span>
        
        {/*   */}
        {rightIcon && (
          <span className="flex-shrink-0 flex items-center justify-center w-4 h-4 text-current ml-auto">
            {rightIcon}
          </span>
        )}
      </button>
    )
  }
)
DropdownItem.displayName = "DropdownItem"

export type DropdownSeparatorProps = DivProps

const DropdownSeparator = React.forwardRef<HTMLDivElement, DropdownSeparatorProps>(
  ({ className, ...props }, ref) => (
    <div
      ref={ref}
      className={cn("h-px bg-gray-200 dark:bg-gray-700 my-1.5 mx-2", className)}
      {...props}
    />
  )
)
DropdownSeparator.displayName = "DropdownSeparator"

export type DropdownLabelProps = DivProps

const DropdownLabel = React.forwardRef<HTMLDivElement, DropdownLabelProps>(
  ({ className, children, ...props }, ref) => (
    <div
      ref={ref}
      className={cn(
        "px-4 py-2 text-xs font-semibold text-gray-500 dark:text-gray-400 uppercase tracking-wide",
        "first:pt-2 last:pb-2",
        className
      )}
      {...props}
    >
      {children}
    </div>
  )
)
DropdownLabel.displayName = "DropdownLabel"

