"use client";

import React from "react";
import { merge } from "../lib/utils";

type SidebarItem = DashboardSidebarSection["items"][number];

export interface DashboardSidebarSection {
  id: string;
  label: string;
  items: Array<{
    id: string;
    label: string;
    href?: string;
    active?: boolean;
    icon?: React.ReactNode;
  }>;
}

export interface DashboardSidebarProps extends React.HTMLAttributes<HTMLDivElement> {
  sections: DashboardSidebarSection[];
  isCollapsed?: boolean;
  onToggleCollapsed?: () => void;
  collapsedWidth?: number;
  expandedWidth?: number;
  mobileBreakpoint?: number;
  logo?: React.ReactNode;
  footer?: React.ReactNode;
}

function useIsMobile(breakpoint = 1024) {
  const [isMobile, setIsMobile] = React.useState(false);
  
  React.useEffect(() => {
    if (typeof window === "undefined") return;
    
    const check = () => setIsMobile(window.innerWidth <= breakpoint);
    check();
    window.addEventListener("resize", check);
    return () => window.removeEventListener("resize", check);
  }, [breakpoint]);
  
  return isMobile;
}

export const DashboardSidebar = React.forwardRef<HTMLDivElement, DashboardSidebarProps>(
  (
    {
      sections,
      isCollapsed = false,
      onToggleCollapsed,
      collapsedWidth = 72,
      expandedWidth = 264,
      mobileBreakpoint = 1024,
      logo,
      footer,
      className,
      ...props
    },
    ref
  ) => {
    const isMobile = useIsMobile(mobileBreakpoint);
    const [hoveredId, setHoveredId] = React.useState<string | null>(null);
    const favoriteItems = React.useMemo(() => {
      const collected: SidebarItem[] = [];
      const seen = new Set<string>();
      sections.forEach((section) => {
        section.items.forEach((item) => {
          if (!seen.has(item.id)) {
            seen.add(item.id);
            collected.push(item);
          }
        });
      });
      return collected.slice(0, 5);
    }, [sections]);

    const width = isCollapsed ? collapsedWidth : expandedWidth;

    const handleClose = () => {
      onToggleCollapsed?.();
    };

    const renderIconGlyph = (item: SidebarItem) => {
      if (item.icon) {
        return item.icon;
      }
      return (
        <span className="text-xs font-semibold text-[#0052CC]">
          {(item.label?.[0] ?? "").toUpperCase()}
        </span>
      );
    };

    return (
      <>
        {/* :  */}
        {isMobile && !isCollapsed && (
          <div
            className="fixed inset-0 z-30"
            style={{ backgroundColor: "rgba(0, 0, 0, 0.3)" }}
            onClick={handleClose}
          />
        )}

        {/*     */}
        {isCollapsed && onToggleCollapsed && !isMobile && (
          <button
            onClick={onToggleCollapsed}
            className="fixed left-2 top-4 z-50 w-8 h-8 bg-white border border-gray-200 rounded-md shadow-md hover:shadow-lg hover:bg-gray-50 transition-all duration-200 flex items-center justify-center"
            aria-label="Expand sidebar"
            title=" "
          >
            <svg
              className="w-4 h-4 text-gray-700"
              fill="none"
              stroke="currentColor"
              viewBox="0 0 24 24"
            >
              <path
                strokeLinecap="round"
                strokeLinejoin="round"
                strokeWidth={2}
                d="M9 5l7 7-7 7"
              />
            </svg>
          </button>
        )}

        <aside
          ref={ref}
          className={merge(
            "flex flex-col h-screen bg-white border-r border-gray-100 transition-all duration-300",
            isMobile ? "fixed left-0 top-0 z-40 shadow-lg" : "relative",
            className
          )}
          style={{ width: isMobile && !isCollapsed ? "80vw" : `${width}px`, maxWidth: isMobile ? 400 : undefined }}
          {...props}
        >
          {/* Logo/Header */}
          {logo && (
            <div className="flex items-center justify-between border-b border-gray-100 flex-shrink-0 p-4">
              {!isCollapsed && <div className="flex-1">{logo}</div>}
              {onToggleCollapsed && !isCollapsed && (
                <button
                  onClick={onToggleCollapsed}
                  className="rounded-sm transition-colors flex-shrink-0 p-1.5 hover:bg-gray-100"
                  aria-label="Collapse sidebar"
                  title=" "
                >
                  <svg
                    className="w-4 h-4 text-gray-600"
                    fill="none"
                    stroke="currentColor"
                    viewBox="0 0 24 24"
                  >
                    <path
                      strokeLinecap="round"
                      strokeLinejoin="round"
                      strokeWidth={2}
                      d="M15 19l-7-7 7-7"
                    />
                  </svg>
                </button>
              )}
            </div>
          )}

          {isCollapsed && favoriteItems.length > 0 && (
            <div className="border-b border-gray-100 px-2.5 pt-4 pb-3 flex flex-col items-center gap-2">
              <span className="text-[10px] uppercase tracking-[0.3em] text-gray-400">
                Favorites
              </span>
              <div className="flex flex-col gap-2 w-full items-center">
                {favoriteItems.map((item) => {
                  const iconNode = (
                    <div className="w-12 h-12 rounded-2xl bg-gradient-to-br from-[#0052CC] to-[#00C897] text-white flex items-center justify-center shadow-md">
                      {item.icon ? (
                        <span className="text-white">{item.icon}</span>
                      ) : (
                        <span className="text-sm font-semibold">
                          {(item.label?.[0] ?? "").toUpperCase()}
                        </span>
                      )}
                    </div>
                  );

                  if (item.href) {
                    return (
                      <a
                        key={`fav-${item.id}`}
                        href={item.href}
                        className="flex flex-col items-center gap-1 text-xs text-gray-500 no-underline"
                        title={item.label}
                      >
                        {iconNode}
                        <span className="sr-only">{item.label}</span>
                      </a>
                    );
                  }

                  return (
                    <div
                      key={`fav-${item.id}`}
                      className="flex flex-col items-center gap-1 text-xs text-gray-500"
                      title={item.label}
                    >
                      {iconNode}
                      <span className="sr-only">{item.label}</span>
                    </div>
                  );
                })}
              </div>
            </div>
          )}

          {/* Navigation Sections */}
          <nav 
            className="flex-1 overflow-y-auto px-4 py-6"
          >
            <div className="flex flex-col gap-4">
              {sections.map((section, sectionIndex) => (
                <div key={section.id}>
                  {/*   (   ) */}
                  {sectionIndex > 0 && (
                    <div className="border-t border-gray-200 mb-4 mt-2" />
                  )}
                  
                  <div className="flex flex-col gap-2.5">
                    {!isCollapsed && (
                      <h3 className="text-xs font-semibold text-gray-400 uppercase tracking-[0.2em] mb-3 pl-4">
                        {section.label}
                      </h3>
                    )}
                    <div className="rounded-xl border border-gray-100 overflow-hidden bg-white/90 dark:bg-gray-900/70">
                      {section.items.map((item) => {
                        const isHovered = hoveredId === item.id;
                        const content = (
                          <div
                            className={merge(
                              "relative flex items-center text-base font-medium transition-all duration-200 cursor-pointer gap-3 px-4 py-3 border-b border-gray-100 last:border-b-0",
                              isCollapsed && "justify-center px-0",
                              item.active
                                ? "bg-brand-primary/10 text-brand-primary shadow-[inset_0_0_0_1px_rgba(0,82,204,0.15)] font-semibold"
                                : "text-gray-700 hover:text-gray-900 hover:bg-gray-50"
                            )}
                            onMouseEnter={() => setHoveredId(item.id)}
                            onMouseLeave={() => setHoveredId(null)}
                          >
                            {/*     */}
                            {item.active && (
                              <div className="absolute left-0 top-0 h-full w-1 rounded-r-full bg-brand-primary" />
                            )}
                            {item.icon && (
                              <span 
                                className="flex-shrink-0 flex items-center justify-center"
                                style={{ width: "20px", height: "20px" }}
                              >
                                {item.icon}
                              </span>
                            )}
                            {!isCollapsed && (
                              <span className="flex-1 truncate">{item.label}</span>
                            )}
                          </div>
                        );

                        if (item.href) {
                          // Next.js Link   Link   
                          //   a  ,   
                          return (
                            <a
                              key={item.id}
                              href={item.href}
                              className="block no-underline"
                              title={isCollapsed ? item.label : undefined}
                              onClick={(e) => {
                                //     preventDefault  
                                // Next.js  
                              }}
                            >
                              {content}
                            </a>
                          );
                        }

                        return (
                          <div key={item.id} title={isCollapsed ? item.label : undefined}>
                            {content}
                          </div>
                        );
                      })}
                    </div>
                  </div>
                </div>
              ))}
            </div>
          </nav>

          {footer && (
            <div className="border-t border-gray-100 p-4">
              {footer}
            </div>
          )}
        </aside>
      </>
    );
  }
);

DashboardSidebar.displayName = "DashboardSidebar";

